.calender-container {
  z-index: 3;
  width: auto;
  display: grid;
  grid-template: auto / repeat(2,1fr);
  grid-gap: 20px;
  /* height: 350px; */
  background-color: #fff;
  position: absolute;
  border-radius: 5px;
  margin-top: 5px;
  padding: 20px 20px 10px;
}
.calender {
  width: 280px;
}
.calender .month-btn {
  position: absolute;
  left: 0;
  width: 40px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.calender .month-btn-right {
  left: auto;
  right: 0;
}
.calender > .caption {
  height: 28px;
  text-align: center;
  font-weight: 700;
}
.calender .week {
  display: grid;
  grid-template: 30px / repeat(7, 40px);
  justify-content: space-between;
  align-items: center;
}
.calender .week-head {
  margin: 10px 0;
}
.calender .week-body {
  grid-template: 40px / repeat(7, 40px);
  height: 100%;
}
.calender .week-label {
  text-align: center;
  color: #979797;
  font-weight: 500;
  height: 100%;
}

.calender .day {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  cursor: pointer;
}
.calender .day::before{
  content: '';
  width: 30px;
  height: 30px;
  z-index: -1;
  position: absolute;
  border-radius: 50%;
}
.calender .day::after{
  content: '';
  z-index: -2;
  width: 40px;
  height: 30px;
  position: absolute;
}
