/* start */
.calender .day-start-single {
  color: white;
  /* background: linear-gradient(90deg, transparent 50%, #c0dee0 0); */
}
.calender .day-start-single::before {
  background-color: #008FA3;
}
.calender .day-start {
  color: white;
  /* background: linear-gradient(90deg, transparent 50%, #c0dee0 0); */
}
.calender .day-start::before {
  background-color: #008fa3;
}
.calender .day-start::after {
  background: linear-gradient(90deg, transparent 50%, #c0dee0 0);
}
/* .calender .day-label-start:before {
  background-color: #008fa3;
  position: absolute;
  border-radius: 50%;
  color: white;
} */

/* in */
.calender .day-in {
  color: white;
  background: linear-gradient(90deg, transparent 0%, #c0dee0 0);
}
/* .calender .day-label-in{
  color: white;
} */
/* end */
.calender .day-end {
  color: white;
  /* background: linear-gradient(270deg, transparent 50%, #c0dee0 0); */
}
/* .calender .day-label-end {
  background-color: #00BCC8;
  border-radius: 50%;
  color: white;
} */
.calender .day-end::before {
  background-color: #008fa3;
}
.calender .day-end::after {
  background: linear-gradient(-90deg, transparent 50%, #c0dee0 0);
}

/* .calender .day-end:active ~.day-label{
  background-color: #008fa3;
} */


/* hover1 */
.calender .day-hover:hover {
  color: white;
}
.calender .day-hover:hover::before {
  background-color: #00BCC8;
}
.calender .day-hover:active::before {
  background-color: #008FA3;
}
/* end-hover1 */
.calender .day-end-hover:hover {
  color: white;
}
.calender .day-end-hover:hover::before {
  background-color: #00BCC8;
}
.calender .day-end-hover:hover::after {
  background: linear-gradient(-90deg, transparent 50%, #c0dee0 0);
}


